import createStore from "./createStore";
import reducer from "./reducer";
import createListItem from "./createListItem";

export const store = createStore(reducer);

store.subscribe(() => console.log(store.getState()));

window.onload = function () {
	const urlInput = document.getElementById("url-input");
	const favoriteList = document.getElementById("favorite-list");
	const bookmarkList = document.getElementById("bookmark-list");

	urlInput.onkeypress = function (eeee) {
		if (eeee.key === "Enter") {
			const url = eeee.target.value;
			const name = nameFromUrl(url)[1];
			const isFav = false;
			const id = UUID();

			store.dispatch({
				type: "ADD_BOOKMARK",
				payload: { url, name, isFav, id },
			});
			eeee.target.value = "";
		}
	};

	// subscribe for all bookmark list
	store.subscribe(() => {
		bookmarkList.innerHTML = null;
		store.getState().forEach(bookmark => {
			let li = createListItem(bookmark);
			bookmarkList.appendChild(li);
		});
	});

	// subscribe for all favorite list
	store.subscribe(() => {
		favoriteList.innerHTML = null;
		store.getState().forEach(bookmark => {
			if (bookmark.isFav) {
				let li = createListItem(bookmark);
				favoriteList.appendChild(li);
			}
		});
	});
};

function nameFromUrl(url) {
	return url.match(/:\/\/(.[^/]+)/);
}

function UUID() {
	var dt = new Date().getTime();
	var uuid = "xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx".replace(
		/[xy]/g,
		function (c) {
			var r = (dt + Math.random() * 16) % 16 | 0;
			dt = Math.floor(dt / 16);
			return (c == "x" ? r : (r & 0x3) | 0x8).toString(16);
		}
	);
	return uuid;
}
